@import 'vars';
@import 'mixins';

.wishlist {
    .wishlist__title {
        margin-top: 20px;
        margin-bottom: 20px;
    }
    .wishlist__content {
        background: #fff;
        .wishlist__settings {
            padding: 15px 10px 30px;
            border-bottom: 1px solid #ececec;
            display: flex;
            justify-content: space-between;
            .btn-clear {
                padding: 10px 20px;
                box-shadow: 0 1px 0 rgba(29, 25, 41, 0.13);
                background-color: #fff;
                border-radius: 3px;
                border: 1px solid #a6a6a6;
                outline: none;
                color: #a6a6a6;
                font-size: 14px;
                font-weight: 700;
                text-transform: uppercase;
                cursor: pointer;
            }
            .wishlist__form {
                display: inline-block;
                margin-left: 20px;
            }
            .wishlist__quantity {
                color: #a6a6a6;
                font-size: 14px;
                display: inline-block;
            }
            .wishlist__subtotal {
                display: inline-block;
                margin-left: 20px;
                margin-right: 30px;
            }
            .total {
                .total__title {
                    color: #a6a6a6;
                    font-size: 14px;
                    text-transform: uppercase;
                    display: inline-block;
                    margin-right: 10px;
                }
                .total__price {
                    color: #2b2b2b;
                    font-size: 14px;
                    font-weight: 700;
                    text-transform: uppercase;
                    display: inline-block;
                }
            }
            .wishlist__btn {
                display: inline-block;
            }
        }
        .wishlist__products {
            @include clearfix;
            padding: 45px 0px;
            .product-horizontal-line {
                margin-left: -5px;
                width: calc(100% + 10px);
            }
            .product-block {
                width: 25%;
                .product-block__inner {
                    padding: 50px 16px;
                    .product-block__sticker {
                        left: 16px;
                    }
                } 
            }
        }
    }
}